<template>
<!-- <br>
<button type="button" class="btnBase font_m" style="padding:10px 10px; margin-left:10px" v-on:click="show">表示</button> -->
<br>
<div class="row">
  <div class="col-6" style="text-align:left">
    <span class="btnBase font_m" v-on:click="pushBtn('BACK')">戻る</span>
  </div>
  <div class="col-6" style="text-align:right">
    <span class="btnBase font_m" v-on:click="pushBtn('ALIGNMENT')">配置はこちら</span>
  </div>
</div>
<br>
<div class="tier">
  <div v-show="isEditTitle" style="display:flex; justify-content:center">
    <input type="text" style="width:60%; height:60px" class="form-control bo2 font_l" v-model="title" maxlength="30" placeholder="タイトルを入力してください">
    <span class="btnBase font_m" style="" v-on:click="pushTitleBtn('OK')">OK</span>
  </div>
  <div v-show="!isEditTitle">
    <span class="font_l">{{ title }}</span>
    <span class="btnBase font_m" style="margin-left:10px" v-on:click="pushTitleBtn('EDIT')">編集</span>
  </div>

  <div id="tierParent" style="margin:auto" class="tierWidth">
    <div v-for="(tierData, index) in tierList" :key="index">
      <div style="display:flex; border:2px solid white;" :style="'background-color:' + tierColorList[tierData.color] + '; min-height:' + (itemHeight + 10) + 'px;'">
        <div style="width:10%; display:flex; align-items:center">
          <p style="width:100%; margin:0; color:black">{{ tierData['name']}}</p>
        </div>
        <div id="box1" class="box" style="text-align:left; width:80%; background-color:black">
          <draggable v-model="tierData['items']" item-key="no" group="ITEMS" style="height:100%; background-color:black;" @end="onEnd" @choose="touchStart($event)">
            <template #item="{ element }">
              <span :id="element.tierNo" style="display:inline-block; padding:5px 2px 5px 2px; cursor:pointer; width:auto; height:auto" :class="itemClass(element)">
                <img v-if="element.type == 'IMAGE'" :src="element.path" :style="'width:' + itemWidth(element.scale) + 'px; height:' + itemHeight + 'px'">
                <span v-if="element.type == 'TEXT'" class="cTierText" :style="'height:' + itemHeight + 'px'">{{ element.path }}</span>
              </span>
            </template>
          </draggable>
        </div>
        <div style="width:10%; display:flex; align-items:center; justify-content: center;">
          <span class="btnBase font_s" style="" v-on:click="pushSetting(index)">設定</span>
        </div>
      </div>
    </div>
    <div id="box2" class="box">
      <draggable v-model="baseItems" item-key="no" group="ITEMS" style="padding:10px; gap: 10px 10px" @end="onEnd"  @choose="touchStart($event)">
        <template #item="{ element }">
          <span :id="element.tierNo" style="display:inline-block; cursor:pointer; width:auto; height:auto" :class="itemClass(element)">
            <img v-if="element.type == 'IMAGE'" :src="element.path" :style="'width:' + itemWidth(element.scale) + 'px; height:' + itemHeight + 'px'">
            <span v-if="element.type == 'TEXT'" class="cTierText" :style="'height:' + itemHeight + 'px'">{{ element.path }}</span>
          </span>
        </template>
      </draggable>
    </div>
  </div>
</div>
<div v-show="this.isCustom">
  ■テキストを追加
  <div>
    <input type="text" placeholder="アイテム名を入力" v-model="customText">
    <span class="btnBase font_l" style="margin-left:10px" v-on:click="pushBtn('ADD_TEXT')">追加</span>
  </div>
  <div class="sp_line_m" />
  ■画像を追加（複数選択可）
  <div>
    <input type="file" multiple accept=".jpg, .png" @change="onFileChange">
  </div>
  <div class="sp_line_m" />
</div>
<span v-show="targetItem != null && targetItem.isCustom == true" class="btnBase font_l" style="" v-on:click="pushBtn('DELETE_ITEM')">選択中のアイテムを削除</span>
<span class="btnBase font_l" style="" v-on:click="pushBtn('RESET')">リセット</span>
<span class="btnBase font_l" style="" v-on:click="pushBtn('SAVE_SCREEN')">保存</span>
<div class="sp_line_m" />
<span class="btnBase font_l" style="" v-on:click="pushBtn('SHARE')">ツイッターにシェア</span>
<div class="sp_line_ss" />
※画像を添付したい場合は、一度保存して、シェアボタンから画像を添付してください。

<!-- スクショ用 -->
<div id="screen" style="position:absolute; left:2500px; top:100px; width:1000px; background-color:#b8e994">
  <span class="font_l">{{ title }}</span>
  <div v-for="(tierData, index) in tierList" :key="index">
    <div style="display:flex; min-height:100px; border:2px solid white;" :style="'background-color:' + tierColorList[tierData.color]">
      <div style="width:100px; display:flex; align-items:center">
        <p style="width:100%; margin:0; color:black">{{ tierData['name']}}</p>
      </div>
      <div id="box1" class="box" style="text-align:left; width:100%; background-color:black">
        <draggable v-model="tierData['items']" item-key="no" group="ITEMS" class="dRow" style="background-color:black; padding:10px" @end="onEnd">
          <template #item="{ element }">
            <span :id="element.tierNo" style="display:inline-block; cursor:pointer; width:auto; height:auto">
              <img v-if="element.type == 'IMAGE'" :src="element.path" :style="'width:' + itemCaptureWidth(element.scale) + 'px; height:100px'">
              <span v-if="element.type == 'TEXT'" class="cTierText" :style="'height:100px'">{{ element.path }}</span>
            </span>
          </template>
        </draggable>
      </div>
    </div>
  </div>
</div>

<div id="overlay" v-show="isShowSetting" v-on:click="closeOverlay($event)">
  <div id="content" style="text-align:center;" class="popupA settingWidth">
    <div style="text-align:center">
      <p class="font_xl">カテゴリー色</p>
      <div style="display:flex; justify-content: center; flex-wrap:wrap">
        <div v-for="(color, index) in tierColorList" :key="index" :style="'width:50px; height:50px; margin:5px; border-radius:50%; background-color:' + color" :class="selectedColor(index)" v-on:click="pushSettingColor(index)" />
      </div>
      <div class="sp_line_l" />
      <p class="font_xl">カテゴリー名</p>
      <input type="text" style="width:80%; margin:auto" class="form-control bo2" v-model="setName" maxlength="10" placeholder="10文字以内">
      <div class="sp_line_l" />
      <span class="btnBase font_l" style="width:50%;" v-on:click="pushSettingBtn('SAVE')">保存</span>
      <div class="sp_line_l" />
      <span v-show="this.selSettingIndex != 0" type="button" class="btnBase font_s" style="width:80%;" v-on:click="chOrder(0)">上</span>
      <div class="sp_line_ss" />
      <span v-show="this.selSettingIndex != tierList.length - 1" type="button" class="btnBase font_s" style="width:80%;" v-on:click="chOrder(1)">下</span>
      <div class="sp_line_xl" />
      <span v-show="this.tierList.length != 1" class="btnBase font_l" style="width:40%; margin-right:10px" v-on:click="pushSettingBtn('DELETE')">削除</span>
      <span class="btnBase font_l" style="width:40%; margin-left:10px" v-on:click="pushSettingBtn('CLEAR')">中身を戻す</span>
      <div class="sp_line_l" />
      <span class="btnBase font_l" style="width:40%; margin-right:10px" v-on:click="pushSettingBtn('ADD_UP')">この上に追加</span>
      <span class="btnBase font_l" style="width:40%; margin-left:10px" v-on:click="pushSettingBtn('ADD_DOWN')">この下に追加</span>
    </div>
  </div>
</div>
</template>

<script>
import draggable from 'vuedraggable'
import axios from 'axios'

export default {
  name: 'Tier',
  components: {
    draggable
  },
  data() {
    return {
      selSettingIndex : 0,
      isShowSetting : false,
      setName : "",
      setColorIndex : 0,

      title : "タイトル未設定",
      isEditTitle : false,

      tierColorList : [
        "#00b894", "#00cec9", "#0984e3", "#6c5ce7", "#fdcb6e", "#e17055", "#d63031", "#e84393"
      ],
      
      tierList : [
        {name:"S", color:0, items:[]},
        {name:"A", color:1, items:[]},
        {name:"B", color:2, items:[]},
        {name:"C", color:3, items:[]},
        {name:"D", color:4, items:[]},
      ],
      baseItems: [],
      categoryData : {},

      isShowScreen : false,

      customText : "",

      hScale : 1.0,

      tierParentWidth : 1000,

      targetItem : null,

      tierNo : 0,
      isCustom : false,
    };
  },
  computed : {
		selectedColor () {
      return (index) => {
        if (index == this.setColorIndex) {
          return "setSelectedColor";
        } else {
          return "";
        }
			}
    },
    itemClass : function () {
      return (item) => {
        let retVal = "";
        if (this.targetItem != null && item.tierNo == this.targetItem.tierNo) {
          retVal = "selectItem";
        }
        return retVal;
      }
    },
    itemWidth : function () {
      return (scale) => {
        if (scale == 0) {
          return this.itemBaseWidth;
        } else {
          return this.itemHeight / scale;
        }
      }
    },
    itemBaseWidth : function () {
      return this.itemHeight / this.hScale;
    },
    itemHeight : function () {
      let val = this.tierParentWidth * 0.8 * 0.15;
      if (val > 100) {
        val = 100;
      }
      return val;      
    }
    ,itemCaptureWidth : function () {
      return (scale) => {
        if (scale == 0) {
          return this.itemBaseWidth;
        } else {
          return this.itemHeight / scale;
        }
      }
    }
  },
  mounted () {
    this.tierParentWidth = document.getElementById("tierParent").clientWidth;
    window.addEventListener('resize', this.calculateWindowWidth);
  },
  beforeRouteLeave(to, from, next) {
    //イベントリスナー解除
    window.removeEventListener('resize', this.calculateWindowWidth);
    next();
  },
  methods : {
    touchStart : function(e){
      console.log(e);
      let tTierNo = e.item.id;
      
      // console.log(tTierNo);
      for (let i = 0; i < this.tierList.length; i++) {
        const items = this.tierList[i].items;
        for (let j = 0; j < items.length; j++) {
          const element = items[j];

          if (tTierNo == element.tierNo) {
            this.targetItem = element;
            console.log(this.targetItem);
            return;
          }
        }
      }

      for (let i = 0; i < this.baseItems.length; i++) {
        if (tTierNo == this.baseItems[i].tierNo) {
          this.targetItem = this.baseItems[i];
          console.log(this.targetItem);
          return;
        }        
      }
    },
    onFileChange : function (e) {
      const files = e.target.files || e.dataTransfer.files;
      console.log(files);

      if (files.length == 0) {
        return;
      }

      for (let i = 0; i < files.length; i++) {
        const element = files[i];

        //形式
        if (element.type == "image/jpeg" || element.type == "image/png") {
          this.createImage(element);
        }
      }
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = e => {
        //画像の縦横を取得
        let img = new Image();
        img.src = e.target.result;
        img.onload = function() {
          let width = img.naturalWidth;
          let height = img.naturalHeight;
          let scale = height / width;
          this.baseItems.push({path:e.target.result, type:"IMAGE", isCustom: true, scale:scale, tierNo:this.tierNo});
          this.tierNo++;
          // console.log(this.baseItems);
        }.bind(this);
      };
      reader.readAsDataURL(file);
    },
    calculateWindowWidth : function () {
      this.tierParentWidth = document.getElementById("tierParent").clientWidth;
    },
    show : function () {
      this.isShowScreen = !this.isShowScreen;
    },
    pushBtn : function(name) {
      if (name == "DELETE_ITEM") {
        //対象のID
        let tTierNo = this.targetItem.tierNo;
        console.log(tTierNo)
        for (let i = 0; i < this.tierList.length; i++) {
          let items = this.tierList[i]["items"];

          for (let j = 0; j < items.length; j++) {
            const element = items[j];
            console.log(j + "   " + element.tierNo);
            if (element.tierNo == tTierNo) {
              items.splice(j, 1);
              this.targetItem = null;
              return;
            }
          }
        }

        for (let i = 0; i < this.baseItems.length; i++) {
          const element = this.baseItems[i];
          console.log(i + "   " + element.tierNo);
          if (element.tierNo == tTierNo) {
            this.baseItems.splice(i, 1);
            this.targetItem = null;
            return;
          }
        }
      } else if (name == "BACK") {
        this.$router.push("/");
      } else if (name == "SAVE_SCREEN") {
        //画像化
        window.html2canvas(document.getElementById('screen'), { 
          scale:1
        }).then(canvas => {
          console.log(canvas.width + " " + canvas.height);
          // var base64 = canvas.toDataURL('image/jpeg');
          // base64 = base64.replace(/^.*,/, '');

          const a = document.createElement("a");
          a.href = canvas.toDataURL("image/jpeg", 1);
          a.download = "image.jpg";
          a.click();

          // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
        });
        this.targetItem = null;
      } else if (name == "RESET") {
        //全てリセット
        for (let i = 0; i < this.tierList.length; i++) {
          let targetList = this.tierList[i].items;

          for (let j = 0; j < targetList.length; j++) {
            let data = targetList[j];
            this.baseItems.push(data);
          }
          this.tierList[i].items = [];
        }
        this.targetItem = null;
      } else if (name == "SHARE") {
        let bodyStr = "【とりとりメーカー】" + this.title;
        let siteUrl = "http://www.yahoo.co.jp";
        var url = "https://twitter.com/intent/tweet?" + 
        "hashtags=" + encodeURIComponent('toritori') + 
        "&text=" + encodeURIComponent(bodyStr) + 
        "&url=" + encodeURIComponent(siteUrl);
        // "&via=" + encodeURIComponent("iphonetaro_live");
        window.open(url);
        this.targetItem = null;
      } else if (name == "ALIGNMENT") {
        if (this.isCustom) {
          this.$router.push("/alignment/tier_custom");
        } else {
          this.$router.push("/alignment/" + this.categoryData.category_id);
        }
      } else if (name == "ADD_TEXT") {
        //カスタムテキストを追加
        if (this.customText == "") {
          return;
        }
        this.baseItems.push({path:this.customText, type:"TEXT", isCustom: true, scale:0, tierNo:this.tierNo});
        this.tierNo++;
        this.targetItem = null;
      }
    },
    pushTitleBtn : function (kbn) {
      if (kbn == "EDIT") {
        this.isEditTitle = true;
      } else if (kbn == "OK") {
        //保存
        this.title = this.title.trim();
        if (this.title == "") {
          this.title = "タイトル未設定";
        }

        this.onEnd();
        this.isEditTitle = false;
      }
      this.targetItem = null;
    },
    pushSetting : function (index) {
      console.log(index);
      this.selSettingIndex = index;
      this.setName = this.tierList[index].name;
      this.setColorIndex = this.tierList[index].color;

      this.isShowSetting = true;
      this.targetItem = null;
    },
    pushSettingBtn : function (name) {
      if (name == "SAVE") {
        //保存
        this.tierList[this.selSettingIndex].name = this.setName;
        this.tierList[this.selSettingIndex].color = this.setColorIndex;
        this.isShowSetting = false;
      } else if (name == "DELETE") {
        //カテゴリー削除
        //まず中身を戻す
        let targetList = this.tierList[this.selSettingIndex].items;
        for (let i = 0; i < targetList.length; i++) {
          let data = targetList[i];
          this.baseItems.push(data);
        }

        //カテゴリーを削除
        this.tierList.splice(this.selSettingIndex, 1);

        this.isShowSetting = false;
      } else if (name == "CLEAR") {
        //中身を戻す
        let targetList = this.tierList[this.selSettingIndex].items;
        for (let i = 0; i < targetList.length; i++) {
          let data = targetList[i];
          this.baseItems.push(data);
        }
        this.tierList[this.selSettingIndex].items = [];
      } else if (name == "ADD_UP" || name == "ADD_DOWN") {
        //データ保存
        this.tierList[this.selSettingIndex].name = this.setName;
        this.tierList[this.selSettingIndex].color = this.setColorIndex;

        let addIndex = this.selSettingIndex;
        if (name == "ADD_DOWN") {
          addIndex = this.selSettingIndex + 1;
        }
        
        //カテゴリを追加
        let after = this.tierList.splice(addIndex);
        Array.prototype.push.apply(this.tierList, [{name:"NEW", color:0, items:[]}]);
        Array.prototype.push.apply(this.tierList, after);

        //アイテムを追加
        this.isShowSetting = false;
      }
    },
    pushSettingColor : function (index) {
      this.setColorIndex = index;
    },
    closeSetting: function() {
			// document.getElementById('body').className = "";
			this.isShowModal = false;
		},
		closeOverlay : function (e) {
			if (e.target.id == "overlay") {
        this.tierList[this.selSettingIndex].name = this.setName;
        this.tierList[this.selSettingIndex].color = this.setColorIndex;
				this.isShowSetting = false;
			}
		},
    onEnd : function() {
      if (this.isCustom) {
        return;
      }

      //現在の配置を保存      
      let saveArray = {title: this.title, tier:this.tierList, base:this.baseItems};
      let json_text = JSON.stringify(saveArray);
      localStorage.setItem("toritori_" + this.categoryData.category_id, json_text);
    },
    chOrder : function (kbn) {
      //順序移動
      let before = this.selSettingIndex;
      let after = this.selSettingIndex;
      if (kbn == 0) {
        //上
        after = this.selSettingIndex - 1;
      } else {
        //下
        after = this.selSettingIndex + 1;
      }
      const value = this.tierList[before];
      const tail = this.tierList.slice(before + 1);
      this.tierList.splice(before);
      Array.prototype.push.apply(this.tierList, tail);
      this.tierList.splice(after, 0, value);
      this.isShowSetting = false;
    },
    getOptions : function() {
      return this.options;
    },
    test : function () {
    },
    initAction : function () {
      let catId = this.$route.params.catId;
      if (catId == "tier_custom") {
        //カスタム
        this.isCustom = true;
        return;
      }

      //データ取得
      let params = new URLSearchParams();
      params.append('categoryId', catId);
      axios.post(this.$root.SERVER_PATH + "getItemData", params)
      .then((response) => {
        let resData = response.data;
        console.log(resData);
        
        //カテゴリー情報
        this.categoryData = resData["categoryData"];

        // localStorage.setItem("toritori_" + this.categoryData.category_id, "");

        //アイテムデータ
        let itemList = resData["itemList"];
        let getItems = [];
        let isSizeCheck = false;
        for (let i = 0; i < itemList.length; i++) {
          const element = itemList[i];

          if (!isSizeCheck && element.item_type == "IMAGE") {
            console.log("IN");
            //カテゴリーの画像サイズを取得
            isSizeCheck = true;
            let img = new Image();
            img.onload = function() {
              var width = img.naturalWidth;
              var height = img.naturalHeight;
              console.log("IMG SIZE " + width + " " + height);
              this.hScale = height / width;
              console.log(this.hScale);
            }.bind(this);
            var path = this.$root.IMG_PATH + this.categoryData.category_id + "/" + element.item_name + '.png';
            console.log(path);
            img.src = path;
          }
          
          getItems.push({path:this.$root.IMG_PATH + this.categoryData.category_id + '/' + element.item_name + '.png', type:element.item_type, isCustom: false, scale:0, tierNo:this.tierNo});
          this.tierNo++;
        }

        //保存してある情報を取得
        let saveStr = localStorage.getItem("toritori_" + this.categoryData.category_id);
        let saveArray = {};
        if (saveStr == null || saveStr == "") {
          //なし デフォルトを設定？
          this.baseItems = getItems;
          this.title = this.categoryData.category_name_ja;
        } else {
          saveArray = JSON.parse(saveStr);
          this.title = saveArray["title"];
          this.tierList = saveArray["tier"];
          this.baseItems = saveArray["base"];

          //データベース上のデータとすり合わせ（追加・削除）
          //保存データにない場合追加
          for (let i = 0; i < getItems.length; i++) {
            const indexName = getItems[i].path;

            let existFlg = false;
            //カテゴリーにあるか
            for (let j = 0; j < this.tierList.length; j++) {
              const tierData = this.tierList[j];
              const items = tierData.items;
              for (let k = 0; k < items.length; k++) {
                const cIndexName = items[k].path;
                if (indexName == cIndexName) {
                  //存在する
                  existFlg = true;
                  break;
                }
              }
            }
            //ベースにあるか
            if (!existFlg) {
              for (let j = 0; j < this.baseItems.length; j++) {
                const bIndexName = this.baseItems[j].path;
                if (indexName == bIndexName) {
                  //存在する
                  existFlg = true;
                  break;
                }
              }
            }

            //ない場合はベースに追加
            if (!existFlg) {
              console.log("追加 " + getItems[i].path);
              this.baseItems.push(getItems[i]);
            }
          }

          //保存データにしかない場合は削除
          //カテゴリー
          for (let i = 0; i < this.tierList.length; i++) {
            const tierData = this.tierList[i];
            const items = tierData.items;
            for (let j = 0; j < items.length;) {
              const cIndexName = items[j].path;
              let existFlg = false;
              for (let k = 0; k < getItems.length; k++) {
                const indexName = getItems[k].path;
                if (cIndexName == indexName) {
                  //存在する
                  existFlg = true;
                  break;
                }
              }
              
              if (!existFlg) {
                //該当行を削除
                console.log("削除C " + items[j].path);
                items.splice(j, 1);
              } else {
                j++;
              }
            }
          }
          //ベース
          for (let j = 0; j < this.baseItems.length;) {
            const bIndexName = this.baseItems[j].path;
            let existFlg = false;
            for (let k = 0; k < getItems.length; k++) {
              const indexName = getItems[k].path;
              if (bIndexName == indexName) {
                //存在する
                existFlg = true;
                break;
              }
            }
            
            if (!existFlg) {
              //該当行を削除
              console.log("削除B " + this.baseItems[j].path);
              this.baseItems.splice(j, 1);
            } else {
              j++;
            }
          }
        }
        console.log("AAAA " + this.hScale);
     })
      .catch((e) => {
        alert(e); 
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

